body {
  font-family: "PT Sans", sans-serif;
  max-width: 320px;
  text-transform: uppercase;
  margin: auto;
  background-color: #f2f2f2;
}

@media (min-width: @tablet-width) {
  body {
    max-width: 768px;
  }
}

@media (min-width: @desktop-width) {
  body {
    max-width: 1280px;
    width: 1280px;
  }
}
